<template>
  <div class="zmb_index">
    <!--  -->
    <div class="zmb_index_banner">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(image, index) in bannerImgs" :key="index">
          <img v-lazy="image" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <!--  -->
    <div class="zmb_index_kinds">
      <div @click="routeCourse">
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <p></p>
      </div>
      <div @click="onevsone">
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <p></p>
      </div>
      <div @click="lzhrili">
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <p></p>
      </div>
    </div>
    <!--  -->
    <section>
      <FTtitle :title="listtitle1"></FTtitle>
      <FamousBox>
        <FamousItem :famous="famous"></FamousItem>
      </FamousBox>
    </section>

    <!--  -->
    <section>
      <FTtitle :title="listtitle2"></FTtitle>
      <ExcellentBox>
        <ExcellentItem :excellent_course="excellent_course"></ExcellentItem>
      </ExcellentBox>
    </section>

    <!--  -->
    <section>
      <FTtitle :title="listtitle3"></FTtitle>
      <RecommendBox>
        <RecommendItem :recommended="recommended"></RecommendItem>
      </RecommendBox>
    </section>

    <!--  -->
    <section>
      <FTtitle :title="listtitle4"></FTtitle>
      <IecturerBox>
        <IecturerItem :lecturer="lecturer"></IecturerItem>
      </IecturerBox>
    </section>

    <div class="box"></div>
  </div>
</template>

<script>
import FTtitle from "../components/FTtitle/FT_title";
import FamousBox from "../components/Home/famousBox";
import FamousItem from "../components/Home/famousItem";
import ExcellentBox from "../components/Home/excellentBox";
import ExcellentItem from "../components/Home/excellentItem";
import RecommendBox from "../components/Home/recommendBox";
import RecommendItem from "../components/Home/recommendItem";
import IecturerBox from "../components/Home/IecturerBox";
import IecturerItem from "../components/Home/lecturerItem";

import Vue from "vue";
import { Lazyload } from "vant";

Vue.use(Lazyload);
export default {
  components: {
    FTtitle,
    FamousBox,
    FamousItem,
    ExcellentBox,
    ExcellentItem,
    RecommendBox,
    RecommendItem,
    IecturerBox,
    IecturerItem,
  },
  data() {
    return {
      //
      bannerImgs: [
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20197Cxc53hktC1569839552.jpg",
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019MGNW3BtiS91569839576.jpg",
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019LnKumseuhw1569839569.jpg",
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20193KAjU2cB6h1569839562.jpg",
      ],
      listtitle1: "",
      listtitle2: "",
      listtitle3: "",
      listtitle4: "",
      famous: [], //
      excellent_course: [], //
      recommended: [], //
      lecturer: [], //
    };
  },
  mounted() {},
  methods: {
    onevsone() {
      this.$router.push("/onevsone");
    },
    routeCourse() {
      this.$router.push("/course");
    },
    lzhrili() {
      this.$router.push("/rili");
    },
  },
  mounted() {
    //
    this.$APP.getteacher().then((res) => {
      this.listtitle1 = res.data.data[4].channel_info.name;
      this.listtitle2 = res.data.data[1].channel_info.name;
      this.listtitle3 = res.data.data[3].channel_info.name;
      this.listtitle4 = res.data.data[0].channel_info.name;
    });
    this.$axios.get("/home.json").then((res) => {
      console.log(res);
      this.famous = res.data.famous; //
      this.excellent_course = res.data.excellent_course; //
      this.recommended = res.data.recommended; //
      this.lecturer = res.data.lecturer; //
    });
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  background: #eee;
}

.zmb_index {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #eee;
}

.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  height: 8rem;
  text-align: center;
  background-color: #39a9ed;
}

.my-swipe .van-swipe-item img {
  width: 100%;
  height: 100%;
}

.zmb_index_kinds {
  width: 100%;
  height: 6rem;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  margin-top: -2rem;
  position: relative;
}

.zmb_index_kinds div {
  width: 30%;
  height: 75%;
  background: white;
  border-radius: 20px;
  font-size: 0.6rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around;
  text-align: center;
}

.zmb_index_kinds div img {
  width: 1.2rem;
  height: 1.2rem;
}

.zmb_index_kinds div p {
  width: 100%;
}
.box {
  width: 100%;
  height: 3rem;
}
</style>